import React from 'react'
import './nav.css';
import NavItem from './nav-item';

const Nav = () => {
  return (
    <nav className="nav">
      <ul className="nav__links-list">
        <NavItem path='/cv' text=''></NavItem>
        <NavItem path='projects' text=''></NavItem>
        <NavItem path='contacts' text=''></NavItem>
      </ul>
    </nav>
  );
};

export default Nav;